import { prisma } from "@/lib/prisma";
import { getToken } from "next-auth/jwt";
import { NextResponse } from "next/server";
import { getMqttClient } from "@/lib/mqttClient";
import {
  getKitchenReadyOrderTopic,
  getStaffCompletedOrderTopic,
} from "@/utils/mqttTopic";

const secret = process.env.NEXTAUTH_SECRET;

export async function PATCH(req, { params }) {
  const token = await getToken({ req, secret });

  if (!token || !["STAFF", "CHEF"].includes(token.role)) {
    return new Response("Ê≤íÊúâÊ¨äÈôê", { status: 403 });
  }

  const { orderId } = params;
  const { status, paymentStatus } = await req.json();

  if (!orderId || (!status && paymentStatus === undefined)) {
    return new Response("Áº∫Â∞ëÂøÖË¶ÅË≥áÊñô", { status: 400 });
  }

  try {
    const data = {};
    if (status) data.status = status;
    if (paymentStatus !== undefined) data.paymentStatus = paymentStatus;
    if (status === "COMPLETED") data.completedAt = new Date();

    const updated = await prisma.order.update({
      where: { id: orderId },
      data,
    });

    if (status === "READY" || status === "COMPLETED") {
      const order = await prisma.order.findUnique({
        where: { id: orderId },
        select: { customerId: true },
      });

      if (order?.customerId) {
        const mqtt = await getMqttClient();
        const topic =
          status === "READY"
            ? getKitchenReadyOrderTopic(order.customerId)
            : getStaffCompletedOrderTopic(order.customerId);

        const payload = JSON.stringify({ orderId, status });

        mqtt.publish(topic, payload, { qos: 1 }, (err) => {
          if (err) {
            console.error("‚ùå ÁôºÈÄÅ MQTT ÈÄöÁü•Â§±Êïó:", err);
          } else {
            console.log(`üì¢ Â∑≤ÁôºÈÄÅ ${status} ÁãÄÊÖãÈÄöÁü•Ëá≥ ${topic}`);
          }
        });
      }
    }

    return NextResponse.json(updated);
  } catch (err) {
    console.error("‚ùå Êõ¥Êñ∞Ë®ÇÂñÆÁãÄÊÖãÂ§±Êïó:", err);
    return new Response("‰º∫ÊúçÂô®ÈåØË™§", { status: 500 });
  }
}
